<template>
  <div id="note" class="detail">
    <NoteSidebar @update:notes="val => notes = val"></NoteSidebar>
    <div class="note-detail">
      <div class="note-empty" v-show="!curNote.id"></div>
      <div class="note-detail-ct" v-show="curNote.id">
        <div class="note-bar">
          <span> : {{ curNote.createdAtFriendly }}</span>
          <span> : {{ curNote.updatedAtFriendly }}</span>
          <span> {{ statusText }}</span>
          <span class="iconfont icon-delete" @click="onDeleteNote"></span>
          <span class="iconfont icon-fullscreen" @click="isShowPreview=!isShowPreview"></span>
        </div>
        <div class="note-title">
          <input type="text" v-model:value="curNote.title" @keydown="statusText='...'"
                 @input="onUpdateNote" placeholder="">
        </div>
        <div class="editor">
          <textarea v-show="!isShowPreview" v-model:value="curNote.content" @input="onUpdateNote"
                    placeholder=",  markdown " @keydown="statusText='...'"></textarea>
          <div class="preview markdown-body" v-html="previewContent" v-show="isShowPreview">
          </div>
        </div>
      </div>
    </div>
  </div>

</template>

<script>

import NoteSidebar from './NoteSidebar'
import _ from 'lodash'
import MarkdownIt from 'markdown-it'
import {mapGetters, mapActions, mapMutations} from 'vuex'

let md = new MarkdownIt()

export default {
  components: {
    NoteSidebar
  },
  data() {
    return {
      statusText: '',
      isShowPreview: false
    }
  },
  created() {
    this.checkLogin({path: '/login'})

  },
  computed: {
    ...mapMutations([
      'setCurNote',
    ]),

    ...mapGetters([
      'notes',
      'curNote'
    ]),

    previewContent() {
      return md.render(this.curNote.content || '')
    }

  },

  methods: {
    ...mapActions([
      'updateNote',
      'deleteNote',
      'checkLogin'
    ]),

    onUpdateNote: _.debounce(function () {
      this.updateNote({noteId: this.curNote.id, title: this.curNote.title, content: this.curNote.content})
        .then(data => {
          this.statusText = ''
        }).catch(data => {
        this.statusText = ''
      })
    }, 500),

    onDeleteNote() {
      this.deleteNote({noteId: this.curNote.id})
        .then(data => {
          this.$router.replace({path: '/note'})
        })
    }
  },

  beforeRouteUpdate(to, from, next) {
    this.$store.commit('setCurNote', {curNoteId: to.query.noteId})
    next()
  }

}
</script>

<style scoped lang="less">
@import url(../assets/css/note-detail.less);

#note {
  display: flex;
  align-items: stretch;
  background-color: #fff;
  flex: 1;
}

::-webkit-scrollbar {

  display: none;
}

</style>



