export function friendlyDate(dateStr) {
  let dateObj = typeof dateStr === 'object' ? dateStr : new Date(dateStr)
  let time = dateObj.getTime()
  let now = Date.now()
  let space = now - time
  let str = ''


  switch (true) {
    case space < 1000 * 60 :
      str = ''
      break
    case space < 1000 * 60 * 60 :
      str = Math.floor((space) / (1000 * 60)) + ''
      break
    case space < 1000 * 60 * 60 * 24 :
      str = Math.floor((space) / (1000 * 3600)) + ''
      break
    default:
      str = Math.floor((space) / (1000 * 3600 * 24)) + ''
      break
  }
  return str
}



