<template>
  <div class="detail" id="notebook-list">
    <header>
      <a href="#" class="btn" @click.stop.prevent="onCreate">
        <i class="iconfont icon-plus"/>
        
      </a>
    </header>
    <main>
      <div class="layout">
        <h3>({{ notebooks.length }})</h3>
        <div class="book-list">
          <router-link :to='`/note?notebookId=${notebook.id}`'
                       v-for="notebook in notebooks" :key="notebook.id"
                       class="notebook">
            <div>
              <span class="iconfont icon-notebook"></span>{{ notebook.title }}
              <span>{{ notebook.noteCounts }}</span>
              <span class="action" @click.stop.prevent="onEdit(notebook)"></span>
              <span class="action" @click.stop.prevent="onDelete(notebook)"></span>
              <span class="date">{{ notebook.friendlyCreatedAt }}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  data() {
    return {}
  },
  created() {
    this.checkLogin({path: '/login'})
    this.getNotebooks()
  },
  computed: {
    ...mapGetters(['notebooks'])
  },
  methods: {
    ...mapActions([
      'getNotebooks',
      'addNotebook',
      'updateNotebook',
      'deleteNotebook',
      'checkLogin'
    ]),
    onCreate() {
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: '30'
      }).then(({value}) => {
        this.addNotebook({title: value})
      })
    },
    onEdit(notebook) {
      let title = ' '
      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: '30'
      }).then(({value}) => {
        this.updateNotebook({notebookId: notebook.id, title: value})
      })
    },
    onDelete(notebook) {
      this.$confirm('?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.deleteNotebook({notebookId: notebook.id})
      })
    }
  }
}
</script>

<style scoped lang="less">
@import url(../assets/css/notebook-list.less);
</style>



